.side-menu{
    margin-right: 3rem;
}
.side-nav{
    background: var(--primary-color);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: calc(100vh - 2.5rem);
    padding: 2rem;
    width: 100px;
    border-radius: 24px;
    -webkit-box-shadow: 15px 15px 20px 0 rgba(135, 135, 135, 0.510);
            box-shadow: 15px 15px 20px 0 rgba(135, 135, 135, 0.510);
    position: relative;
    overflow: hidden;
}

.side__menu{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.brand img{
    height: 40px;
    width: auto;
}

.menu__item{
    display: inline-block;
    margin-bottom: 64px;
}
.menu__item img{
   width: 24px;
    height: auto;
}

button.logout img{
    height: 25px;
    width: auto;
}



